export const carList = (type : any) => {
    const type1 = ['니로(DE) EV','EV3','EV6','아이오닉 5 EV (롱레인지)', '아이오닉 6'];
    const type2 = ['봉고 EV','포터 EV'];

    switch (type) {
        case '01' :
            return type1;
        case '02' :
            return type2;
    }
}
